import * as React from "react";
import * as ToastPrimitives from "@radix-ui/react-toast";
import { X } from "lucide-react";
import { cn } from "../utils/cn";

const ToastProvider = ToastPrimitives.Provider;

const ToastViewport = React.forwardRef<
  React.ElementRef<typeof ToastPrimitives.Viewport>,
  React.ComponentPropsWithoutRef<typeof ToastPrimitives.Viewport>
>(({ className, ...props }, ref) => (
  <ToastPrimitives.Viewport
    ref={ref}
    className={cn(
      `fixed left-0 top-0 z-[100] flex max-h-screen w-full flex-col-reverse p-4 
      sm:bottom-0 sm:left-auto sm:right-0 sm:top-auto sm:flex-col md:max-w-[420px]`,
      className
    )}
    {...props}
  />
));
ToastViewport.displayName = ToastPrimitives.Viewport.displayName;

const Toast = React.forwardRef<
  React.ElementRef<typeof ToastPrimitives.Root>,
  React.ComponentPropsWithoutRef<typeof ToastPrimitives.Root>
>(({ className, ...props }, ref) => {
  return (
    <ToastPrimitives.Root
      ref={ref}
      className={cn(
        `group pointer-events-auto relative flex w-full items-center justify-between 
      space-x-4 overflow-hidden rounded-md border p-6 pr-8 shadow-lg transition-all 
      data-[swipe=cancel]:translate-x-0 
      data-[swipe=end]:translate-x-[var(--radix-toast-swipe-end-x)] 
      data-[swipe=move]:translate-x-[var(--radix-toast-swipe-move-x)] 
      data-[swipe=move]:transition-none 
      data-[state=open]:animate-in 
      data-[state=closed]:animate-out 
      data-[swipe=end]:animate-out 
      data-[state=closed]:fade-out-80 
      data-[state=closed]:exit-translate-x-full 
      data-[state=open]:-enter-translate-y-full 
      sm:data-[state=open]:enter-translate-y-full`,
        className
      )}
      {...props}
    />
  );
});
Toast.displayName = ToastPrimitives.Root.displayName;

const ToastAction = React.forwardRef<
  React.ElementRef<typeof ToastPrimitives.Action>,
  React.ComponentPropsWithoutRef<typeof ToastPrimitives.Action>
>(({ className, ...props }, ref) => (
  <ToastPrimitives.Action
    ref={ref}
    className={cn(
      `inline-flex 
      h-8 
      shrink-0
      items-center
      justify-center 
      rounded-md 
      border 
      bg-transparent 
      px-3 text-sm font-medium ring-offset-2 transition-colors hover:bg-pink-500 focus:outline-none 
      focus:ring-2 focus:ring-pink-200 focus:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 
      `,
      className
    )}
    {...props}
  />
));
ToastAction.displayName = ToastPrimitives.Action.displayName;

const ToastClose = React.forwardRef<
  React.ElementRef<typeof ToastPrimitives.Close>,
  React.ComponentPropsWithoutRef<typeof ToastPrimitives.Close>
>(({ className, ...props }, ref) => (
  <ToastPrimitives.Close
    ref={ref}
    className={cn(
      `absolute right-2 top-2 rounded-md p-1 text-black/50 opacity-0 transition-opacity 
      focus:opacity-100 focus:outline-none focus:ring-2 focus:ring-cyan-700
      group-hover:opacity-100 
      group-[.destructive]:text-red-300 
      group-[.destructive]:hover:text-red-500 
      group-[.destructive]:focus:ring-red-400 
      group-[.destructive]:focus:ring-offset-red-600`,
      className
    )}
    toast-close=""
    {...props}
  >
    <X className="h-4 w-4" />
  </ToastPrimitives.Close>
));
ToastClose.displayName = ToastPrimitives.Close.displayName;

const ToastTitle = React.forwardRef<
  React.ElementRef<typeof ToastPrimitives.Title>,
  React.ComponentPropsWithoutRef<typeof ToastPrimitives.Title>
>(({ className, ...props }, ref) => (
  <ToastPrimitives.Title
    ref={ref}
    className={cn("text-sm font-semibold", className)}
    {...props}
  />
));
ToastTitle.displayName = ToastPrimitives.Title.displayName;

const ToastDescription = React.forwardRef<
  React.ElementRef<typeof ToastPrimitives.Description>,
  React.ComponentPropsWithoutRef<typeof ToastPrimitives.Description>
>(({ className, ...props }, ref) => (
  <ToastPrimitives.Description
    ref={ref}
    className={cn("text-sm opacity-90", className)}
    {...props}
  />
));
ToastDescription.displayName = ToastPrimitives.Description.displayName;

type ToastProps = React.ComponentPropsWithoutRef<typeof Toast>;

type ToastActionElement = React.ReactElement<typeof ToastAction>;

export {
  type ToastProps,
  type ToastActionElement,
  ToastProvider,
  ToastViewport,
  Toast,
  ToastTitle,
  ToastDescription,
  ToastClose,
  ToastAction,
};
